@include r(640) {
  .hidden-sm {
    display: none; } }
@include rmin(641) {
  .hidden-xs {
    display: none; } }



.section-open {
  transition: transform 0.86s cubic-bezier(0.74, -0.01, 0.36, 1);
  &.is-active {
    position: fixed;
    transform: translateY(0) translateX(-40%) scale(0.83) rotateY(-5deg);
    @include r(640) {
      transform: translateY(0) translateX(-100%) scale(1); } } }

.landscape {
  display: none; }

@media (max-width: 640px) and (orientation: landscape) {
  .landscape {
    position: fixed;
    z-index: 1000;
    display: block;
    width: 100%;
    height: 101%;
    background: #fff; }
  .landscape__block {
    position: absolute;
    top: 50%;
    left: 50%;
    flex-direction: column;
    transform: translate(-50%, -50%);
    img {
      display: inline-block;
      width: 150px;
      animation: Mobile 2s infinite ease-in-out; }
    p {
      font-family: 'Proxima Nova Rg';
      font-size: 30px;
      margin-top: 20px;
      text-align: center; } } }

.flex {
  display: flex; }

html {
  box-sizing: border-box;
  width: 100%;
  height: 100%; }



body {
  font-family: 'Proxima Nova Rg';
  position: fixed;
  overflow: hidden;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  // perspective: 1000px
  &.is-active {
    overflow-y: visible; }
  &.no-hidden {
    overflow: visible;
    position: static; } }

p, h1, h2, h3, h4 {
  margin: 0;
  padding: 0; }

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none; }

a {
  cursor: pointer;
  text-decoration: none; }

.btn {
  position: relative;
  z-index: 8;
  box-sizing: border-box;
  width: 80%;
  cursor: pointer;
  transition: all .3s;
  display: block;
  &__block {
    position: relative;
    transition: all .2s ease;
    transition-overflow: hidden;
    font-family: 'Proxima Nova Rg';
    font-size: 16px;
    font-weight: 600;
    position: absolute;
    z-index: 10;
    right: -8px;
    bottom: -8px;
    display: flex;
    box-sizing: border-box;
    width: 100%;
    height: 60px;
    transition: all .3s;
    letter-spacing: 5px;
    text-transform: uppercase;
    color: #fff;
    border: 2px solid #000;
    justify-content: center;
    align-items: center; }
  .open {
    position: relative;
    z-index: 9;
    display: flex;
    height: 60px;
    transition: all .3s;
    letter-spacing: 5px;
    text-transform: uppercase;
    color: #fff;
    background: #f52e38;
    justify-content: center;
    @include r(640) {
      display: none; } } }

.btn {
  .mobile {
    display: none; }
  .mobile__close {
    position: relative;
    top: 50px;
    left: 15px;
    display: none;
    &:before, &:after {
      position: absolute;
      display: inline-block;
      width: 70px;
      content: '';
      border: 2px solid #fff; }
    &:before {
      transform: rotate(45deg); }
    &:after {
      transform: rotate(-45deg); } }
  &:hover .btn__block {
    right: 0;
    bottom: 0; } }

