#preloader {
  position: fixed;
  z-index: 100;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background: #fff;
  img {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 150px;
    transform: translate(-50%, -50%); } }

@media (max-width: 640px) {
  .ripplelink {
    position: relative;
    overflow: hidden;
    width: 91%;
    transition: all .2s ease; } }

.ink {
  display: none; }

@media (max-width: 640px) {
  .ink {
    position: absolute;
    display: block;
    transform: scale(0);
    border-radius: 100%;
    background: rgba(0, 0, 0, 0.3); } }

@media (max-width: 640px) {
  .animate {
    animation: ripple 1s linear; } }

@keyframes ripple {
  100% {
    transform: scale(2.5);
    opacity: 0; } }


