.close, .close-form {
  font-family: 'Proxima Nova Rg';
  font-size: 12px;
  position: absolute;
  position: fixed;
  top: 70px;
  right: -35px;
  display: inline-block;
  padding: 15px 15px 15px 50px;
  cursor: pointer;
  transition: all .3s;
  transform: rotate(90deg);
  animation-duration: 1s;
  animation-timing-function: ease;
  letter-spacing: 4px;
  text-transform: uppercase;
  color: #999;
}

.close:hover, .close-form:hover {
  animation-name: Tada;
  color: #f52e38;
}

.close:hover span:before, .close:hover span:after, .close-form:hover span:before, .close-form:hover span:after {
  background-color: #f52e38;
}

.close:hover span:before, .close-form:hover span:before {
  transform: rotate(45deg) translate(6px, 5px);
}

.close:hover span:after, .close-form:hover span:after {
  transform: rotate(-45deg) translate(9px, -8px);
}

.close span, .close-form span {
  position: relative;
  top: 6px;
  left: -125px;
  background-color: transparent;
}

.close span:after, .close span:before, .close-form span:after, .close-form span:before {
  position: absolute;
  top: -10px;
  left: 0;
  content: '';
  transition: all .3s;
}

