  top: 10px;
  width: 36px;
  height: 4px;
  content: '';
  transform: rotate(-53deg) translate(9px, -8px);
  background-color: #999;
  backface-visibility: hidden;
}

.close span:before, .close-form span:before {
  width: 36px;
  height: 4px;
  content: '';
  transform: rotate(53deg) translate(6px, 5px);
  background-color: #999;
  backface-visibility: hidden;
}

